"use client";

import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { FiUsers, FiHeart, FiTrendingUp, FiAlertTriangle, FiRefreshCw, FiTrendingDown } from "react-icons/fi";
import { DateRangePicker } from "../components/charts/DateRangePicker";
import { BarChart } from "../components/charts/BarChart";
import { PieChart } from "../components/charts/PieChart";
import { ProgressCircle } from "../components/charts/ProgressCircle";
import { DataTable } from "../components/charts/DataTable";
import { CalendarChart } from "../components/charts/CalendarChart";
import Navbar from "../components/Navbar";

type Patient = {
    id: string;
    name: string;
    lastVisit: string;
    status: string;
    createdAt: string; // Ajout de la date de création
};

type Stats = {
    totalPatients: number;
    averageAge: number;
    genders: { gender: string; count: number }[];
    diagnoses: { diagnosis: string; count: number }[];
    recentPatients: Patient[];
};

export default function ModernDashboard() {
    const [stats, setStats] = useState<Stats | null>(null);
    const [loading, setLoading] = useState(true);
    const [dateRange, setDateRange] = useState({
        start: new Date(new Date().setMonth(new Date().getMonth() - 3)),
        end: new Date()
    });

    useEffect(() => {
        const fetchData = async () => {
            try {
                setLoading(true);
                const params = new URLSearchParams({
                    start: dateRange.start.toISOString(),
                    end: dateRange.end.toISOString()
                });

                const res = await fetch(`/api/stats?${params}`);
                if (!res.ok) {
                    throw new Error(`HTTP error! status: ${res.status}`);
                }
                const data = await res.json();
                setStats(data);
            } catch (error) {
                console.error("Fetch error:", error);
            } finally {
                setLoading(false);
            }
        };

        fetchData();
    }, [dateRange]);

    if (loading) {
        return (
            <div className="flex justify-center items-center min-h-screen bg-gray-50">
                <div className="text-center">
                    <div className="w-16 h-16 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto"></div>
                    <p className="mt-4 text-lg font-medium text-gray-600">
                        Chargement des données...
                    </p>
                </div>
            </div>
        );
    }

    if (!stats) {
        return (
            <div className="flex justify-center items-center min-h-screen bg-gray-50">
                <div className="text-center">
                    <p className="mt-4 text-lg font-medium text-gray-600">
                        Aucune donnée disponible.
                    </p>
                </div>
            </div>
        );
    }

    // Calcul des nouveaux patients par jour
    const newPatientsPerDay = stats.recentPatients.reduce((acc, patient) => {
        const date = patient.createdAt.split('T')[0]; // Extrait la date (YYYY-MM-DD)
        acc[date] = (acc[date] || 0) + 1;
        return acc;
    }, {});

    // Calcul du nombre total de cas critiques
    const criticalCases = stats.recentPatients.filter(patient => patient.status === 'Critique').length;

    return (
        <div className="min-h-screen bg-gray-50">
            <Navbar />

            {/* Header */}
            <header className="bg-white shadow-sm">
                <div className="max-w-7xl mx-auto px-4 py-4 sm:px-6 lg:px-8 flex justify-between items-center">
                    <h1 className="text-2xl font-bold text-gray-900">Tableau de bord AI4CKD</h1>
                    <div className="flex items-center space-x-4">
                        <DateRangePicker value={dateRange} onChange={setDateRange} />
                        <button
                            className="p-2 rounded-full bg-gray-100 hover:bg-gray-200"
                            onClick={() => {
                                // re-fetch data on click
                                const fetchData = async () => {
                                    try {
                                        setLoading(true);
                                        const params = new URLSearchParams({
                                            start: dateRange.start.toISOString(),
                                            end: dateRange.end.toISOString()
                                        });

                                        const res = await fetch(`/api/stats?${params}`);
                                        if (!res.ok) {
                                            throw new Error(`HTTP error! status: ${res.status}`);
                                        }
                                        const data = await res.json();
                                        setStats(data);
                                    } catch (error) {
                                        console.error("Fetch error:", error);
                                    } finally {
                                        setLoading(false);
                                    }
                                };

                                fetchData();
                            }}
                        >
                            <FiRefreshCw className="text-gray-600" />
                        </button>
                    </div>
                </div>
            </header>

            <main className="max-w-7xl mx-auto px-4 py-6 sm:px-6 lg:px-8">
                {/* KPI Cards */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8"
                >
                    <KpiCard
                        icon={<FiUsers className="text-blue-500" size={24} />}
                        title="Patients"
                        value={stats.totalPatients || 0}
                        change="+12%"
                        trend="up"
                    />
                    <KpiCard
                        icon={<FiHeart className="text-green-500" size={24} />}
                        title="Âge Moyen"
                        value={stats.averageAge ? stats.averageAge.toFixed(1) : "N/A"}
                        change="+2.5%"
                        trend="up"
                    />
                    <KpiCard
                        icon={<FiTrendingUp className="text-purple-500" size={24} />}
                        title="Nouveaux Cas (Jour)"
                        value={Object.values(newPatientsPerDay).reduce((a, b) => a + b, 0)} // Total des nouveaux cas
                        change="+8%"
                        trend="up"
                    />
                    <KpiCard
                        icon={<FiAlertTriangle className="text-red-500" size={24} />}
                        title="Cas Critiques"
                        value={criticalCases}
                        change="-3%"
                        trend="down"
                    />
                </motion.div>

                {/* Reste du code (graphiques, tableaux, etc.) */}
            </main>
        </div>
    );
}

// Component for KPI Cards
function KpiCard({ icon, title, value, change, trend }) {
    return (
        <div className="bg-white p-6 rounded-xl shadow-sm hover:shadow-md transition">
            <div className="flex justify-between">
                <div>
                    <p className="text-sm font-medium text-gray-500">{title}</p>
                    <p className="text-3xl font-bold mt-2 text-gray-900">{value}</p>
                </div>
                <div className="h-10 w-10 rounded-full bg-gray-50 flex items-center justify-center">
                    {icon}
                </div>
            </div>
            <div className={`mt-4 flex items-center text-sm ${trend === "up" ? "text-green-600" : "text-red-600"}`}>
                {trend === "up" ? <FiTrendingUp className="mr-1" /> : <FiTrendingDown className="mr-1" />}
                {change}
                <span className="text-gray-500 ml-1">vs période précédente</span>
            </div>
        </div>
    );
}